.l-pages {
  padding: 50px 0;

  &_head {
    h1 {
      font-size: 35px;
      letter-spacing: 0.02em;
    }
    margin-bottom: 50px;
  }

  &_content {
    > section {
      margin-bottom: 50px;
      h2 {
        font-size: 20px;
        margin-bottom: 15px;
      }
      p {
        line-height: 1.7;
        > a {
          color: cornflowerblue;
          display: inline;
          &:hover {
            text-decoration: underline;
          }
        }
        + p {
          margin-top: 20px;
        }
      }
    }
    &_H3 {
      margin-top: 20px;
      margin-bottom: 20px;
      font-size: 14px;
      > h3 {
        font-size: 16px;
        margin-bottom: 15px;
      }
    }
  }
  &_table {
    width: 100%;
    th,td {
      border: 1px solid $color-black;
      text-align: center;
      vertical-align: middle;
      border-collapse: collapse;
      min-width: 107px;
    }
    tbody td {
      height: 40px;
      &[data-status="○"] {
        background-color: $color-green;
      }
      &[data-status="△"] {
        background-color: $color-yellow;
      }
      &[data-status="☓"] {
        background-color: $color-red;
      }
    }
  }
}